.fondo{
    
    background-image: url('../../img/fondo.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    flex-direction: column;
    min-height:100vh; 
}
.formAct{
    align-items: center;
    justify-content:center;
    border-radius:5vw;
    background: rgb(0,0,0);
    background: linear-gradient(0, rgba(0,0,0,0.6) 50%, rgba(34, 41, 43, 0.6) 100%);
    margin:10vw ;
    padding:10vw;
    display:flex;
    flex-direction: column;
    /* width:35vw; */
    width: 60%;
    text-align: center;
}

    .tittles{
        text-decoration: none;
    
        padding: 2vw;
        font-size: 4vw;
        min-height:10%;
        /* margin:auto; */
        text-align: center;
        font-family: Playball;

    }
  
    .inputs{
        display:flex;
        flex-direction:column;
        justify-content:space-around;
        min-height:90%;
        color: whitesmoke;
        font-family: Playball;
    }
    .name{
        margin-top: 0.8%;
        display:flex;
        justify-content: space-between;
        font-size: 1.8vw;
        font-family: Playball;
        color: rgb(74, 175, 197);
    }
