.containerCard{

	display: flex;
	flex-wrap: wrap;
	justify-content:center;
	margin: auto;
	align-content: flex-start;

}
.card p{
	font-size: 1.3vw;
	color:black;
	font-family:playball;
}
.card{
	background-color: #42d4b0 !important;
	background: radial-gradient(circle, #b4b4b4, #42d4b0);
	padding: 0.4vw;
	width: 15vw;
	border-radius: 8px;
	margin: 1.5vw;
	text-align: center;
	transition: all 0.25s;

}
.card:hover{
	transform: translate(-15px);
	box-shadow: 0 12px 16px rgba(0, 0, 0, 0.2);
}
.imageCard{
	width: 95%;

}
.containerVermas{

	font-family:Helvetica;
	font-size: 1vw;
	background-color:#424a5e;
	border-radius: 1vw;
	height: 2vw;
	/* padding: 0.5; */
    
}
.containerVermas:hover{
	background-color: rgb(66, 65, 65);
	border: solid 0.1vw;
	border-color: rgba(255, 0, 0, 0);

}
.txtVermas{
	font-family:Cinzel;
	text-decoration: none;
	color: white;
	padding: 1vw;
	font-size: 1vw;
	text-align: center;

}
.cardName{
	font-size: 2vw;
	margin: 1vw;
	font-family:Cinzel;
	padding: 1.5%;
	color: rgb(255, 255, 255);
}
